$break-mobile: 500px;
$break-small: 767px;

.App {
  // background-color: #282c34;
  // background-color: #243135;
  background-image: url(./background-sm.png);
  background-size: cover;
  // background-size: auto 100%;
  background-position: center;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  min-height: 100vh;
}

.lines {
  background-image: url(./lines.png);
  background-size: auto 100%;
  background-position: center;
  z-index: 2;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
}

.hero {
  height: 100vh;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;  
  width: 100%;
}

.center-container {
  margin: 0 auto;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  &.flex {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.landing-container {
  display: flex;
  padding-top: 50px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
  @media screen and (max-width: $break-mobile) {
    padding-top: 30px;
  }
}

$orange: rgb(221, 125, 69);

.mdc-button--raised {
  background-color: $orange !important;
  color: #fff !important;
  width: 150px;
  border-radius: 6px !important;
  font-size: .8rem !important;
  letter-spacing: 2px;
}

.mdc-button::before, .mdc-button::after {
  background-color: $orange;
}

.mdc-floating-label--float-above {
  color: $orange !important;
  opacity: 0;
}

.mdc-line-ripple {
  background-color: $orange !important;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.text-center {
  text-align: center;
}

.email-container {
  margin-top: -110px;
  background-image: url(./stripe.png);
  padding: 5rem 1rem 1rem;
  background-size: cover;
  background-position: center center;
  width: calc(100% - 2rem);
  flex-grow: 1;
  position: relative;
}

.sign-up {
  margin: 1rem 0;
}

.logo {
  height: 100px;
  @media screen and (max-width: $break-mobile) {
    height: 120px;
  }
}

.coming-soon {
  height: 50px;
  margin-top: 20px;
}

.input-container {
  > div, button {
    margin: 1rem .5rem;
  }
}

.mdc-text-field {
  border-radius: 0;
  height: 36px;
  background-color: rgb(171, 171, 171) !important;
  width: 250px;

  .mdc-floating-label {
    top: 11px;
    color: black !important;
    font-size: .9rem !important;
  }

  input {
    padding: 0 0 0 10px;
  }
}

@media screen and (max-width: $break-small) {
  .sign-up {
    height: 10px;
  }
  
  .input-container {
    flex-direction: column;

      > div, button {
        margin: .4rem;
      }

      > div, .mdc-text-field, button {
        @media screen and (max-width: $break-mobile) {
          width: 100%;
        }
      }
    }
  }

@media screen and (max-width: $break-mobile) {
  .coming-soon {
    height: 40px;
  }

  .email-container {
    margin-top: -200px;
    padding-top: 9rem;
  }
}

.social-list {
  display: flex;
  justify-content: center;
  padding: 0;
  margin-bottom: 0;
  margin-top: 1rem;
  li {
    margin: .5rem;
    img {
      height: 15px;
      transition: .25s ease-in-out all;
    }
    &:hover {
      img {
        filter: brightness(200%);
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }
    }
  }
}

.main-body {
  display: flex;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}