body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-header {
  padding-top: 1rem;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  font-size: .9rem; }

a {
  color: #fff;
  text-decoration: none; }

.main-header a {
  font-weight: bold;
  letter-spacing: 1px; }

.social-icon {
  color: #fff; }

li {
  list-style: none; }

.main-menu.social {
  float: right;
  margin-right: 10%; }
  .main-menu.social ul {
    margin: 0; }
  .main-menu.social li {
    padding: 0; }
  @media screen and (max-width: 500px) {
    .main-menu.social {
      margin-right: 1rem; } }

.main-menu {
  display: inline-block; }

.main-menu ul, .main-menu li {
  display: inline-block;
  padding: 0; }

.main-menu li {
  padding: 1rem;
  text-transform: uppercase; }

.logo-container {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 1.2rem;
  top: 0; }

.logo-container a {
  color: #fff;
  font-size: 2rem; }

footer {
  padding: 1rem; }

.copyright {
  font-size: 9px;
  text-align: center;
  color: #999999; }


.album-art.landing {
  width: auto;
  height: 417px;
  padding: 1rem 0;
  z-index: 10; }

.sign-up {
  height: 12px; }

.hero.store {
  background-image: url("https://scontent.fbed1-1.fna.fbcdn.net/v/t1.0-9/205603_1004680764979_9025_n.jpg?_nc_cat=105&_nc_ht=scontent.fbed1-1.fna&oh=a5ec2016e06a23d78741640867ec25aa&oe=5CB553CD");
  background-size: cover;
  background-position: 0% 60%;
  -webkit-filter: grayscale(100%) contrast(130%) brightness(50%);
          filter: grayscale(100%) contrast(130%) brightness(50%); }

.hero.contact {
  background-image: url("https://scontent.fbed1-2.fna.fbcdn.net/v/t1.0-9/299682_2083099401728_1147421824_n.jpg?_nc_cat=107&_nc_ht=scontent.fbed1-2.fna&oh=ad5229e3e4973034e0087ac004f45be3&oe=5CC5C4B2");
  background-size: cover;
  background-position: 50% 20%;
  -webkit-filter: grayscale(100%) contrast(130%) brightness(50%);
          filter: grayscale(100%) contrast(130%) brightness(50%); }

.contact-wrapper > div {
  padding: 0 3.5rem; }

.hero.tour {
  background-image: url("https://scontent.fbed1-2.fna.fbcdn.net/v/t1.0-9/200216_1002686072070_979_n.jpg?_nc_cat=102&_nc_ht=scontent.fbed1-2.fna&oh=2da484e736bd601aad2b1fc7de60e11b&oe=5CB6D886");
  background-size: 150% auto;
  -webkit-filter: blur(5px) grayscale(100%) contrast(120%) brightness(50%);
          filter: blur(5px) grayscale(100%) contrast(120%) brightness(50%);
  background-position: 0% 20%; }

.App {
  background-image: url(/rope-and-ladder/static/media/background-sm.e22c44b7.png);
  background-size: cover;
  background-position: center;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  min-height: 100vh; }

.lines {
  background-image: url(/rope-and-ladder/static/media/lines.b3be9f4c.png);
  background-size: auto 100%;
  background-position: center;
  z-index: 2;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none; }

.hero {
  height: 100vh;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }

.center-container {
  margin: 0 auto;
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }
  .center-container.flex {
    display: flex;
    justify-content: center;
    align-items: center; }

.landing-container {
  display: flex;
  padding-top: 50px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 1; }
  @media screen and (max-width: 500px) {
    .landing-container {
      padding-top: 30px; } }

.mdc-button--raised {
  background-color: #dd7d45 !important;
  color: #fff !important;
  width: 150px;
  border-radius: 6px !important;
  font-size: .8rem !important;
  letter-spacing: 2px; }

.mdc-button::before, .mdc-button::after {
  background-color: #dd7d45; }

.mdc-floating-label--float-above {
  color: #dd7d45 !important;
  opacity: 0; }

.mdc-line-ripple {
  background-color: #dd7d45 !important; }

.flex {
  display: flex; }

.flex-center {
  justify-content: center;
  align-items: center; }

.text-center {
  text-align: center; }

.email-container {
  margin-top: -110px;
  background-image: url(/rope-and-ladder/static/media/stripe.480c4234.png);
  padding: 5rem 1rem 1rem;
  background-size: cover;
  background-position: center center;
  width: calc(100% - 2rem);
  flex-grow: 1;
  position: relative; }

.sign-up {
  margin: 1rem 0; }

.logo {
  height: 100px; }
  @media screen and (max-width: 500px) {
    .logo {
      height: 120px; } }

.coming-soon {
  height: 50px;
  margin-top: 20px; }

.input-container > div, .input-container button {
  margin: 1rem .5rem; }

.mdc-text-field {
  border-radius: 0;
  height: 36px;
  background-color: #ababab !important;
  width: 250px; }
  .mdc-text-field .mdc-floating-label {
    top: 11px;
    color: black !important;
    font-size: .9rem !important; }
  .mdc-text-field input {
    padding: 0 0 0 10px; }

@media screen and (max-width: 767px) {
  .sign-up {
    height: 10px; }
  .input-container {
    flex-direction: column; }
    .input-container > div, .input-container button {
      margin: .4rem; } }
  @media screen and (max-width: 767px) and (max-width: 500px) {
    .input-container > div, .input-container .mdc-text-field, .input-container button {
      width: 100%; } }

@media screen and (max-width: 500px) {
  .coming-soon {
    height: 40px; }
  .email-container {
    margin-top: -200px;
    padding-top: 9rem; } }

.social-list {
  display: flex;
  justify-content: center;
  padding: 0;
  margin-bottom: 0;
  margin-top: 1rem; }
  .social-list li {
    margin: .5rem; }
    .social-list li img {
      height: 15px;
      transition: .25s ease-in-out all; }
    .social-list li:hover img {
      -webkit-filter: brightness(200%);
              filter: brightness(200%);
      box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22); }

.main-body {
  display: flex;
  min-height: 100vh;
  width: 100%;
  overflow: hidden; }

